.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
  padding: 23px;
  width: 100%;
  max-height: 700px;
  min-width: 40px;
  background-color: white;
  z-index: 1;
}

.product__rating {
    display: flex;
}
  
.product > img {
  max-height: 180px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}
  
  .product > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    width: 70%;
    max-width: 200px;
  }

  .product__price {
    margin-top: 5px;
  }
  
  .product__info {
    height: 50%;
    margin-bottom: 15px;
  }

.btn{
  border-radius: 10px;
  font-size: 17px;
}
  
